define('qa-board/components/new-question', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    addNewQuestion: false,
    actions: {
      questionFormShow: function questionFormShow() {
        this.set('addNewQuestion', true);
      },

      saveQuestion: function saveQuestion() {
        var params = {
          content: this.get('content'),
          questionAuthor: this.get('questionAuthor'),
          description: this.get('description')
        };
        this.set('content', ''), this.set('questionAuthor', ''), this.set('description', ''), this.set('addNewQuestion', false), this.sendAction('saveQuestion', params);
      }
    }
  });

});