define('qa-board/components/question-detail', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    actions: {
      saveAnswer: function saveAnswer(params) {
        this.sendAction('saveAnswer', params);
      },

      voteUpAnswer: function voteUpAnswer(answer) {
        this.sendAction('voteUpAnswer', answer);
      },
      voteDownAnswer: function voteDownAnswer(answer) {
        this.sendAction('voteDownAnswer', answer);
      },

      deleteAnswer: function deleteAnswer(answer) {
        this.sendAction('deleteAnswer', answer);
      },

      deleteQuestion: function deleteQuestion(question) {
        if (confirm('Are you sure you want to delete this question?')) {
          this.sendAction('deleteQuestion', question);
        }
      }
    }
  });

});