define('qa-board/components/new-answer', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    addNewAnswer: false,
    actions: {
      answerFormShow: function answerFormShow() {
        this.set('addNewAnswer', true);
      },

      saveAnswer: function saveAnswer() {
        var params = {
          answerContent: this.get('answerContent') ? this.get('answerContent') : "",
          answerAuthor: this.get('answerAuthor') ? this.get('answerAuthor') : "",
          date_added: Date.now(),
          votes: 0,
          question: this.get('question') ? this.get('question') : ""
        };
        this.set('anwerContent', ''), this.set('answerAuthor', ''), this.set('addNewAnswer', false), this.sendAction('saveAnswer', params);
      }
    }
  });

});