define('qa-board/routes/question', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    model: function model(params) {
      return this.store.findRecord('question', params.question_id);
    },
    actions: {
      saveAnswer: function saveAnswer(params) {
        var newAnswer = this.store.createRecord('answer', params);
        var question = params.question;
        question.get('answers').addObject(newAnswer);
        newAnswer.save().then(function () {
          return question.save();
        });
        this.transitionTo('question', params.question);
      },

      deleteAnswer: function deleteAnswer(answer) {
        answer.destroyRecord().then(function () {
          question.save();
        });
        this.transitionTo('question');
      },

      voteUpAnswer: function voteUpAnswer(answer) {
        answer.set('votes', answer.get('votes') + 1).then(function () {
          question.save();
        });
        this.transitionTo('question');
      },

      voteDownAnswer: function voteDownAnswer(answer) {
        answer.set('votes', answer.get('votes') - 1).then(function () {
          question.save();
        });
        this.transitionTo('question');
      },

      deleteQuestion: function deleteQuestion(question) {
        question.destroyRecord();
        this.transitionTo('index');
      },

      updateQuestion: function updateQuestion(question, params) {
        Object.keys(params).forEach(function (key) {
          if (params[key] !== undefined) {
            question.set(key, params[key]);
          }
        });
        question.save();
        this.transitionTo('index');
      }
    }
  });

});