define('qa-board/components/update-question', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    updateQuestionForm: false,
    actions: {
      updateQuestionForm: function updateQuestionForm() {
        this.set('updateQuestionForm', true);
      },
      updateQuestion: function updateQuestion(question) {
        var params = {
          content: this.get('content'),
          questionAuthor: this.get('questionAuthor'),
          description: this.get('description')
        };
        this.set('content', ''), this.set('questionAuthor', ''), this.set('description', ''), this.set('updateQuestionForm', false);
        this.sendAction('updateQuestion', question, params);
      }
    }
  });

});