define('qa-board/models/answer', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  exports['default'] = DS['default'].Model.extend({
    answerContent: DS['default'].attr(),
    answerAuthor: DS['default'].attr(),
    date_added: DS['default'].attr(),
    votes: 0,
    question: DS['default'].belongsTo('question', { async: true })
  });

});